<template>
    <div id="playground-area">
        <div class="name-area">
            Opponent name: <span class="your-name" id="opponent-name">{{opponent.name}}</span>
        </div>
        <div class="cancel-game">                        
            <div style="margin-right: 10px">
                <button id="cancel-game-btn" @click="cancelGame" :class="!gaming ? 'disabled' : ''" :disabled="!gaming">Cancel Game</button>
            </div>
            <div style="margin-left: 10px">
                <button id="go-home-btn" @click="goto('/')">Go Home</button>
            </div>
        </div>
        <div class="play-turn" id="play-turn" v-if="gaming">{{playTurn}}</div>
        <div class="play-turn" id="play-turn" v-else>Waiting for opponent ... <span>{{count}}</span></div>
        <div style="padding: 35px;" v-if="!gaming">
            <OrbitSpinner
            :animation-duration="1000"
            :size="200"
            color="teal"
            />
        </div>
        <div id="playground" v-else>
            <div class="playground-row" 
            v-for="n in 3" :key="n" 
            :style="n < 3 ? 'border-bottom: 1px solid teal' : ''">
                <div class="playground-box" 
                v-for="i in 3" :key="i" 
                :style="i < 3 ? 'border-right: 1px solid teal' : ''" 
                @click="PlayerMove({moveID: getID(`${n}${i}`)})" 
                :id="getID(`${n}${i}`)"></div>
            </div>
        </div>
        <div id="scores">
            <div class="score">
                <div>You</div>
                <div>{{player.won}}</div>
            </div>
            <div class="score">
                <div>Draw</div>
                <div>{{player.draw}}</div>
            </div>
            <div class="score">
                <div>Opponent</div>
                <div>{{opponent.won}}</div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from 'vuex';
import * as messages from '.././messages';
import Dialog from './Dialog';
import { OrbitSpinner } from 'epic-spinners'

export default {
    computed: {
        ...mapGetters(['opponent', 'player', 'playGroundElements', 'playTurn', 'loading', 'gaming']),
    },
    mounted (){
        this.UPDATE_PLAYGROUND()
        this.countDown()
    },
    components: {
        Dialog, OrbitSpinner
    },
    data() {
        return {
            showDialog: false,
            count: 10,
        }
    },
    methods: {
        ...mapActions(['PlayerMove', 'CancelGame']),
        ...mapMutations(['UPDATE_DIALOG', 'UPDATE_PLAYGROUND']),
        clickedBox(n) {
            const id = this.getID(n)
            const playGroundElement = this.playGroundElements[id]
            if (playGroundElement == undefined || playGroundElement == null) {
                this.$store.dispatch('PlayerMove', this.getID(n))
                return
            }
        },
        getID(n) {
            return `id-${n}`
        },
        goto(url) {
            this.$router.push({path:url})
        },
        cancelGame() {
            this.UPDATE_DIALOG({
                active: true,
                dialogHeader: 'Cancel Game With Opponent?',
                dialogType: messages.MESSAGE_PLAYER_EXIT_GAME,
                dialogCancelText: 'No',
                dialogOKText: 'Yes',
                dialogOK: true,
                dialogCancel: true,
            })
        },
        async countDown() {
            const sleep = (milliseconds) => {
                return new Promise(resolve => setTimeout(resolve, milliseconds))
            }
            for (let i = 9; i >= 0; i--) {
                await sleep(1000)
                this.count = i
            }
        }
    }
}
</script>